import { useState } from "react";

function Add({ notes, setNote, d, init }) {
  const [obj, setObj] = useState(init());

  function addItem() {
    setNote([...notes, obj]); /* добавляем запись [obj] в стейт */
    setObj(init()); /* очищаем поле */
  }

  return (
    <input
      value={obj.data}
      onChange={(e) => setObj({ ...obj, [d]: e.target.value })}
      onBlur={() => addItem()}
    />
  );
}

export default Add;
