import Element from "./Element";

function List({ notes, invert, del, edit, p, p1, d }) {
  // notes - стейт
  // invert - функция инвертирования свойства
  // del - функция удаления
  // edit - функция редактирования
  // p - свойство "isDone"
  // p1 - свойство "isEdit"
  // d - свойство "data"
  const result = notes.map((v, i) => {
    return (
      <Element
        key={v.id}
        id={v.id} /* id */
        i={i} /* порядковый номер */
        v={v.data} /* текст стейта */
        isDone={v.isDone} /* флаг выполнения */
        isEdit={v.isEdit} /* флаг редактирования */
        invert={invert} /* функция инвертирования свойства */
        del={del} /* функция удаления */
        edit={edit} /* функция редактирования */
        p={p} /* имя свойства p="isDone" */
        p1={p1} /* имя свойства p1="isEdit" */
        d={d} /* имя свойства d="data" */
      />
    );
  });

  return (
    <>
      <div>
        <table>
          <tbody>{result}</tbody>
        </table>
      </div>
    </>
  );
}

export default List;
