import List from "./List";
import Add from "./Add";
import { v4 as uuidv4 } from "uuid";
import { useState } from "react";

const notes = [
  {
    id: uuidv4(),
    data: "JFHyurx6s7r",
    isEdit: false,
    isDone: false
  },
  {
    id: uuidv4(),
    data: "kcsjdb89w",
    isEdit: false,
    isDone: false
  },
  {
    id: uuidv4(),
    data: "csbdilu t734",
    isEdit: false,
    isDone: false
  },
  {
    id: uuidv4(),
    data: "cn u9877tr3c",
    isEdit: false,
    isDone: false
  }
];
const p = "isDone";
const p1 = "isEdit";
const d = "data";

function init() {
  const record = {
    id: uuidv4(),
    data: "",
    isEdit: false,
    isDone: false
  };

  return record;
}

function Todo() {
  const [note, setNote] = useState(notes); /* стейт данных */

  function invert(id, prob) {
    // функция инвертирует в стейте
    // свойство [prob]
    // по id
    setNote(
      note.map((v) => {
        if (v.id === id) {
          let value = !v[prob];
          return { ...v, [prob]: value };
        } else return v;
      })
    );
  }

  function del(id) {
    // функция удаляет элемент с указанным id
    setNote(note.filter((v) => v.id !== id));
  }

  function edit(id, prob, e) {
    setNote(
      note.map((v) => {
        if (v.id === id) {
          return { ...v, [prob]: e.target.value };
        } else return v;
      })
    );
  }

  return (
    <>
      <div>
        <List
          notes={note}
          invert={invert}
          del={del}
          edit={edit}
          p={p}
          p1={p1}
          d={d}
        />
        <Add notes={note} setNote={setNote} d={d} init={init} />
      </div>
    </>
  );
}

export default Todo;
