function Element({ id, i, v, isDone, isEdit, invert, del, edit, p, p1, d }) {
  // id - id
  // i - порядковый номер
  // v - текст стейта
  // isDone - флаг выполнения
  // isEdit - флаг редактирования
  // invert - функция инвертирования свойства
  // del - функция удаления
  /* edit - функция редактирования */
  // [p] - имя свойства p="isDone"
  // [p1] - имя свойства p1="isEdit"
  // d = "data";

  return (
    <tr className={isDone ? "strike" : ""}>
      <td>{i + 1}</td>
      <td>
        {!isEdit ? (
          <span onClick={() => invert(id, p1)}>{v}</span>
        ) : (
          <input
            value={v}
            onChange={(e) =>
              edit(id, d, e)
            } /* записываем в свойство [d] текст e.target.value */
            onBlur={() =>
              invert(id, p1)
            } /* по окончании редактирования инвертируем свойство [p1] */
          />
        )}
      </td>
      <td>
        <button onClick={() => invert(id, p)}>
          {!isDone ? "Выполнить" : "Отменить"}
        </button>
      </td>
      <td>
        <button onClick={() => del(id)}>x</button>
      </td>
    </tr>
  );
}

export default Element;
